<!-- Vue.js Record Pager -->
<template>
    <div style="user-select: none;" class="record-pager">
        <div v-if="debug">
            <hr> numRecords: {{numRecords}}, pageSize: {{_pageSize}}, pageCount: {{_pageCount}}, mode: {{mode == "block" ? mode
            :"std"}}, totalRecords: {{totalRecords}},
            <br> selectedPageSize: {{selectedPageSize}}, currentPage: {{currentPage}},
            <br> pageSizes: {{JSON.stringify(pageSizes)}},
        </div>
        <!-- <transition name="fade" mode="out-in"> -->
        <div v-if="_pageCount>0 && records.length>0" style="position: relative;white-space: nowrap;">
            <select v-show="pageSizes.length>1" v-model="selectedPageSize" class="form-control-static" style="user-select:none; position:relative;top:-32px;color:#337ab7; border-radius:4px;border-color:#ddd;">
                <option 
                        v-for="option in pageSizes" 
                        :value="option.size"                         
                        :key="option.size">{{option.text}}
                </option><!-- :selected="option.selected"  -->
            </select>
            <paginate :click-handler="pageChanged" ref="pager" :page-count="_pageCount" :margin-pages="1" :page-range="2" :initial-page="0"
                :container-class="'pagination'" :page-class="'page-item'" :page-link-class="'page-link-item'" :prev-class="'prev-item'"
                :prev-link-class="'prev-link-item'" :next-class="'next-item'" :next-link-class="'next-link-item'">
            </paginate>
        </div>
        <!-- </transition> -->
    </div>
</template>

<script>
    import paginate from 'vuejs-paginate';
    export default {
        components: {
            paginate
        },
        props: {
            debug: {
                default: false
            },
            updatePage: {
                default: null,
                type: String
            },
            pageCount: {
                default: "0",
                type: String
            },
            pageSizes: {
                default: () => [
                    { size: 5, text: "5 items", selected: true },
                    { size: 10, text: "10 items" },
                    { size: 25, text: "25 items" },
                    { size: 50, text: "50 items" }
                ],
                type: Array
            },
            records: {
                default: () => [],
                type: Array
            },
            totalRecords: {
                default: 0,
                type: Number
            },
            pagerStyle: {
                default: () => ({
                    mode: "bootstrap",
                    styles: {

                    }
                }),
                type: Object
            },
            mode: {
                default: null, // standard mode
                type: String
            },
            id: {
                default: "pager",
                type: String
            }
        },
        data() {
            return {
                // _pageCount: 0,
                currentPage: 1,
                selectedPageSize: 5,
                forcePage: 0,
            }
        },
        methods: {
            pageChanged(page) {
                // console.log('pageChanged: ' + page);
                this.currentPage = page;
            },
            // selectedChanged(e) {
            //     console.log('selectedChanged: ', e.target.value);
            //     this.selectedPageSize = e.target.value;
            //     this.$refs.pager.selected = 0;
            //     this.currentPage = 1;
            // },

            // updatePageSizes() {
            //     let option = this._pageSizes.find(option => option.selected);
            //     console.log("updatePageSizes _pageSizes option:", option);
            //     this.selectedPageSize = option ? option.size : this._pageSizes[0].size;
            // },
            updatePageSizes() {
                let option = this.pageSizes.find(opt => opt.selected);
                console.log("updatePageSizes pageSizes option:", option);
                this.selectedPageSize = option ? option.size : this.pageSizes[0].size;
            },
            updateData(records) {
                console.log('updateData');
                // bad: tightly couples component to parent
                // if (this.$parent.pagerController) {
                //     this.$parent.pagerController({ id: this.id, recordsPage: this.recordsPage });
                //     return;
                // }
                this.$emit('controller', { // event args
                    name: 'pageUpdated',
                    id: this.id,
                    recordsPage: this.recordsPage
                }); // also emit event for those wanting to wire up manually
                this.$emit('pageUpdated', this.recordsPage); // also emit event for those wanting to wire up manually
            }
        },
        computed: {
            _pageCount() {
                let count = this.pageCount;
                if (this._pageSize > 0) {
                    count = Math.ceil(this.numRecords / this._pageSize);
                }
                console.log('pageCount', count);
                // console.log('numRecords', this.numRecords);
                return count;
            },
            _pageSize() {
                console.log('_pageSize', this.selectedPageSize)
                return parseInt(this.selectedPageSize);
            },
            // _pageSizes() {
            //     console.log("computed _pageSizes changed");
            //     let _default = [
            //         { size: 5, text: "5 items", selected: true },
            //         { size: 10, text: "10 items" },
            //         { size: 25, text: "25 items" },
            //         { size: 50, text: "50 items" }
            //     ];
            //     return this.pageSizes || _default;
            // },
            recordsPage() {
                let page;
                let start = (this.currentPage - 1) * this._pageSize;
                let end = start + this._pageSize;
                if (this.mode == "block") {
                    page = this.records.slice(start, end);
                    // console.log('RecordsPage', start, end, page);
                }
                else {
                    // console.log('RecordsPage: totalRecords !=0')
                    page = this.records.slice(0, this._pageSize);
                }
                return page;
            },
            numRecords() {
                // return this.totalRecords || this.records.length;
                return this.mode == "block" ? this.records.length : this.totalRecords;
            },
            // computed_selectedPageSize: function (newValue) {
            //     console.log('COMPUTED: selectedPageSize', newValue);
            //     if (this.$refs.pager) { // if pager is rendered
            //         this.$refs.pager.selected = 0;
            //     }
            //     this.currentPage = 1;
            //     if (this.mode == "block") {
            //         this.updateData(this.recordsPage);
            //         return;
            //     }
            //     this.$emit('pageArgsChanged', { pageNum: this.currentPage, pageSize: newValue, id: this.id });
            //     this.$emit('controller', { // event args
            //         name: 'pageArgsChanged',
            //         id: this.id,
            //         pageNum: this.currentPage,
            //         pageSize: newValue
            //     });
            // },
        },
        watch: {
            currentPage: function (newValue) {
                console.log('watch: currentPage', newValue);
                if (this.mode == "block") {
                    console.log('watch: currentPage NO pageArgsChanged');
                    this.updateData(this.recordsPage);
                    return;
                }
                console.log('watch: currentPage pageArgsChanged');
                this.$emit('pageArgsChanged', { pageNum: newValue, pageSize: this._pageSize, id: this.id });
                this.$emit('controller', { // event args
                    name: 'pageArgsChanged',
                    id: this.id,
                    pageNum: newValue,
                    pageSize: this._pageSize
                });
            },

            selectedPageSize: function (newValue) {
                console.log('watch: selectedPageSize', newValue);
                if (this.$refs.pager) { // if pager is rendered
                    this.$refs.pager.selected = 0;
                }
                this.currentPage = 1;
                if (this.mode == "block") {
                    this.updateData(this.recordsPage);
                    return;
                }
                if (this.records.length>0) { // this check necessary to prevent pageArgsChanged from firing when pageSizes passed in as props
                    this.$emit('pageArgsChanged', { pageNum: this.currentPage, pageSize: newValue, id: this.id });
                    this.$emit('controller', { // event args
                        name: 'pageArgsChanged',
                        id: this.id,
                        pageNum: this.currentPage,
                        pageSize: newValue
                    });
                }
            },

            recordsPage: function () {
                console.log('watch: recordsPage');
                // if (!this.updatePage) {
                //     this.$emit('recordsPageUpdated', this.recordsPage);
                //     return;
                // }
                this.updateData(this.recordsPage);
            },
            pageSizes: function (newVal, old) {
                console.log("watch: _pageSizes changed", newVal, old);
                this.updatePageSizes();
            }
        },
        beforeCreate() {
            // console.log('beforeCreate props:', this.$props); // props = undefined
        },
        created() {
            console.log('RecordPager created, props:', this.$props);
            this.updatePageSizes();
        },
        mounted() {
            // console.log('mounted props:', this.$props);
            // this.updatePageSizes();
        },
        beforeUpdate() {
            // console.log('beforeUpdate props:', this.$props);
        },
        updated() {

        }
    }

</script>

<style>

</style>


